.loader {
  width: 100%;
  height: 4.8px;
  background: rgba(255, 255, 255, .15);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 0;
  height: 4.8px;
  background: #464444;
  animation: animFw 10s linear infinite;
  position: absolute;
  top: 40%;
  left: 40%;
}

@keyframes animFw {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

